
.card{
    display:flex;
    width: 100%;
    margin-left: 2em;
    gap: 2em;

}
.red{
    color:#F55A5A;
}
.imageCard{
    overflow: hidden;
    display: flex;
    justify-content: center;
    border-radius: 0.5em;
    aspect-ratio: 4/5;
    width: 27%;
}
img{
    width: 27em;
}
.cardDescription{
    width: 60%;
}
.country{
    font-weight: 400;
    font-size: 1em;
    margin: 0;
    display: inline;
}
.map-link{
    font-size: 0.8em;
    margin-left: 1em;
    color: #555555;
    display: inline;
}
h1{
    font-size: 1rem;
}
@media screen and ( max-width: 300px ){
    .map-link{
        display: block;
    }
    .country{
        display: block;
    }
}
