html{
    font-size: 1.2vw;
    font-family: 'Inter', sans-serif;
}

body{
    width: 100%;
    background-color: #e5e5e5;
}
#root{
    display: flex;
    justify-content: center;
    width:60%;
    margin: 0 auto;
    
}


.App{

    display: flex;
    width: 100%;
    justify-content: center;
    flex-direction: column;
    background-color: #f5f5f5;
    gap: 2rem;



}
@media screen and ( max-width: 50em){
    html{ font-size: 1.5vw;}
}

