.header{
    background-color: #F55A5A;
    width: 100%;
    margin: 0 0 3em 0;
    box-shadow: 0 0 2px rgba(0,0,0,0.3);
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
    color: white;
    padding: 1em 0;
    text-align: center;
}
.header-text{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    gap: 1rem;
}







